<template>
  <div class="research">
    <div class="section-content">
      <h1>research</h1>
      
      <div class="research-wrapper">
        <div class="research-filter-section">
          <div class="research-search">
            <div class="search-container">
              <i class="fas fa-search search-icon"></i>
              <input 
                type="text" 
                v-model="searchQuery" 
                placeholder="Search research papers..." 
                class="search-input"
                @input="filterResearch"
              />
              <button v-if="searchQuery" @click="clearSearch" class="clear-search">
                <i class="fas fa-times"></i>
              </button>
            </div>
          </div>
          
          <div class="filter-section">
            <h2>Filter by Category</h2>
            <div class="filter-buttons">
              <button 
                v-for="category in categories" 
                :key="category" 
                :class="{ active: selectedCategory === category }"
                @click="filterByCategory(category)"
              >
                <i :class="getCategoryIcon(category)"></i>
                {{ category }}
              </button>
              <button 
                :class="{ active: selectedCategory === 'All' }"
                @click="filterByCategory('All')"
              >
                <i class="fas fa-globe"></i>
                All
              </button>
            </div>
          </div>

          <div class="research-stats">
            <div class="stat-item">
              <div class="stat-value">{{ research.length }}</div>
              <div class="stat-label">Total Publications</div>
            </div>
            <div class="stat-item">
              <div class="stat-value">{{ categories.length }}</div>
              <div class="stat-label">Research Areas</div>
            </div>
            <div class="stat-item">
              <div class="stat-value">{{ filteredResearch.length }}</div>
              <div class="stat-label">Showing</div>
            </div>
          </div>
        </div>
        
        <div v-if="filteredResearch.length" class="research-grid">
          <div 
            v-for="item in filteredResearch" 
            :key="item.id"
            class="research-card"
            :class="{ 'expanded': expandedItems.includes(item.id) }"
          >
            <div class="research-header" @click="toggleExpanded(item.id)">
              <div class="research-icon">
                <i :class="getCategoryIcon(item.category)"></i>
              </div>
              <div class="research-category-tag">{{ item.category }}</div>
              <div class="research-meta">
                <span class="research-year">{{ item.year }}</span>
                <span class="research-status">{{ item.status }}</span>
              </div>
              <h3 class="research-title">{{ item.title }}</h3>
              <p class="research-author">{{ item.author }}</p>
              <p class="research-summary">{{ item.summary }}</p>
              
              <div class="research-tags">
                <span v-for="(tag, index) in item.tags" :key="index" class="tag">
                  {{ tag }}
                </span>
              </div>
              
              <div class="expand-indicator">
                <i :class="expandedItems.includes(item.id) ? 'fas fa-chevron-up' : 'fas fa-chevron-down'"></i>
                <span>{{ expandedItems.includes(item.id) ? 'Less Details' : 'More Details' }}</span>
              </div>
            </div>
            
            <div v-if="expandedItems.includes(item.id)" class="research-details">
              <div class="details-content">
                <h4>Abstract</h4>
                <p>{{ item.details }}</p>
                
                <div v-if="item.links && item.links.length" class="research-links">
                  <h4>Resources</h4>
                  <ul>
                    <li v-for="(link, index) in item.links" :key="index">
                      <a :href="link.url" target="_blank" class="resource-link">
                        <i class="fas fa-external-link-alt"></i>
                        {{ link.text }}
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
              
              <button class="close-btn" @click.stop="toggleExpanded(item.id)">
                <i class="fas fa-times"></i>
                Close
              </button>
            </div>
          </div>
        </div>
        
        <div v-else class="no-results">
          <div class="no-results-icon">
            <i class="fas fa-search"></i>
          </div>
          <h3>No matching research found</h3>
          <p>Try adjusting your search or filters to find what you're looking for.</p>
          <button @click="resetFilters" class="reset-filters-btn">
            <i class="fas fa-redo"></i>
            Reset Filters
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResearchView',
  data() {
    return {
      research: [],
      categories: [],
      selectedCategory: 'All',
      expandedItems: [],
      searchQuery: '',
      filteredResults: []
    }
  },
  computed: {
    filteredResearch() {
      if (!this.searchQuery && this.selectedCategory === 'All') {
        return this.research;
      }
      
      return this.research.filter(item => {
        const matchesCategory = this.selectedCategory === 'All' || item.category === this.selectedCategory;
        
        if (!this.searchQuery) {
          return matchesCategory;
        }
        
        const query = this.searchQuery.toLowerCase();
        const matchesSearch = 
          item.title.toLowerCase().includes(query) ||
          item.author.toLowerCase().includes(query) ||
          item.summary.toLowerCase().includes(query) ||
          (item.details && item.details.toLowerCase().includes(query)) ||
          (item.tags && item.tags.some(tag => tag.toLowerCase().includes(query)));
        
        return matchesCategory && matchesSearch;
      });
    }
  },
  methods: {
    filterByCategory(category) {
      this.selectedCategory = category;
    },
    toggleExpanded(id) {
      const index = this.expandedItems.indexOf(id);
      if (index === -1) {
        // Close any open items first for accordion-like behavior
        this.expandedItems = [id];
      } else {
        this.expandedItems.splice(index, 1);
      }
    },
    filterResearch() {
      // The actual filtering is handled by the computed property
    },
    clearSearch() {
      this.searchQuery = '';
    },
    resetFilters() {
      this.searchQuery = '';
      this.selectedCategory = 'All';
      this.expandedItems = [];
    },
    getCategoryIcon(category) {
      const icons = {
        'Machine Learning': 'fas fa-brain',
        'Deep Learning': 'fas fa-network-wired',
        'Computer Vision': 'fas fa-eye',
        'Natural Language Processing': 'fas fa-language',
        'Reinforcement Learning': 'fas fa-robot',
        'Data Science': 'fas fa-chart-line',
        'Explainable AI': 'fas fa-lightbulb',
        'Neural Networks': 'fas fa-project-diagram',
        'Signal Processing': 'fas fa-wave-square'
      };
      
      return icons[category] || 'fas fa-microscope';
    },
    async loadResearchData() {
      try {
        // In a real app, you'd import the JSON directly or use fetch
        const researchData = require('@/data/research.json');
        this.research = researchData;
        
        // Extract unique categories
        const categoriesSet = new Set(this.research.map(item => item.category));
        this.categories = Array.from(categoriesSet);
      } catch (error) {
        console.error('Error loading research data:', error);
      }
    }
  },
  mounted() {
    this.loadResearchData();
  }
}
</script>

